@import (less) "color.less";

.nbp-modal--opened {
  display: flex !important;
  flex-direction: column;
  align-items: center;
}

.nbp-modal {
    display: none;
    position: fixed;
    z-index: 10000;
    /*padding-top: 100px;*/
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.5);
     &.no-static-backdrop__small{
      width: 34.375em;
    }
    &.container__medium{
      width: 64em;
    }
    &.container__large{
      width: 85.375em;
    }
    &.inside-error-modal{
      z-index: 150000 !important;
    }
}

.nbp-modal-title {
    color: #323232;
    font-size: 18px;
    padding-top:20px
}

.nbp-modal-sub-title {
    text-transform: capitalize;
    color: #323232;
    padding-top:20px
}

.close-icon-container {
  position: relative;
  & > .icon-times{
    position: absolute;
    right:20px;
    top:20px;
    width: 19px;
    height: 19px;
    font-size: 19px;
    z-index: 10;
    color: @green;
    cursor: pointer;
  }
}

.mrg-l-5 {
  margin-left: 5px;
  margin-right: 5px;
}

.nbp-modal-body-with-title {
  padding: 20px 15px 20px 15px;
}

.nbp-modal-body-without-title {
  padding: 40px;
}

.container-fluid {
  line-height: 1;
}

.nbp-modal-container {
    max-height: 90vh;
    overflow: auto;
    overflow:visible; //modificareply
    position: relative;
    background-color: #fefefe;
    margin: auto;
    padding-bottom: 20px;
    border: 1px solid #888;
    //top: 50%;
    //transform: translateY(-50%);
    &.container__small{
      max-width: 34.5em;
      min-width: 17.375em;
    }
    &.container__medium{
      max-width: 64em;
      min-width: 32em;
    }
    &.container__large{
      max-width: 85.375em;
      min-width: 42.375em;
    }
}

.nbp-close-modal {
    color: #aaaaaa;
    font-size: 28px;
    font-weight: bold;
      &:hover,
      &:focus {
        color: #000;
        text-decoration: none;
        cursor: pointer;
      }
}




:host-context(.contrasto-on) {
  .close-icon-container {
    & > .icon-times{
      color: @black;
    }
  }
}
